/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.title {
  font-family: 'Courier New', Courier, monospace;
  font-size: 32px;
  text-align: center;
  margin-top: 24px; }

.btns {
  align-items: center;
  justify-content: center;
  display: flex; }

.save-btn, .load-btn {
  border-radius: 50px;
  font-family: 'Courier New', Courier, monospace;
  padding: 12px 24px;
  margin: 24px 10px;
  font-size: 16px;
  border: 0;
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.4s; }
  .save-btn:hover, .load-btn:hover {
    opacity: 0.5; }

.save-btn {
  background-color: lightpink; }

.load-btn {
  background-color: aquamarine; }

#cards {
  margin-bottom: 30px; }

.list-cards {
  display: grid;
  grid-template-columns: 1fr 1fr; }
  .list-cards .card {
    border-radius: 50px;
    text-align: center;
    margin: 10px;
    padding: 15px;
    background-color: aquamarine;
    font-size: 20px;
    font-family: 'Courier New', Courier, monospace;
    cursor: pointer;
    opacity: 1;
    transition: opacity 0.4s; }
    .list-cards .card:hover {
      opacity: 0.5; }
    .list-cards .card.card-friend {
      background-color: lightpink; }
    .list-cards .card .image {
      border: 10px solid white;
      border-radius: 60px;
      margin-bottom: 10px; }

@media (max-width: 600px) {
  .list-cards {
    grid-template-columns: 1fr; } }
